import React, { useEffect, useState } from 'react';
import { io } from 'socket.io-client'; // Websocket
import Chatbox from '../Component/Chatbox';
import Bell from "../Iphone.mp3"  // Notification RIng
import { toast } from 'react-toastify';  // Importing toastifiers

const socket = io.connect("http://localhost:5000"); // COnnecting new user/ socket ("connection" route [23])


export default function Basics() {

  const [data, setData] = useState({ username: "", code: "" }); // handling input data
  const [showchat, setShowchat] = useState(false); // handling rendering of chatbox
  const notificaton = new Audio(Bell); // creating instence of AUdio to use

  // handling onChange Input
  const handleValue = (e) => {
    setData({ ...data, [e.target.name]: e.target.value })
    console.log(data)
  }

  // when user click on join chat, it will works, check for input constraints if all good then emit to "join_room"
  const join_chat = () => {

    if (isNaN(data.code)) { toast("Enter Number only."); }

    else if (data.username == "") { toast("Name Feild Empty."); }

    else if (data.username !== "" && data.code !== "") { //if all good, connect to backend "join_room" [27]
      socket.emit("join_room", data.code, data.username); // sending code,username to backend
      notificaton.play(); // playing sound on join
      setShowchat(true); // setting true to show chat box
      toast.success("Joined Chat sucessfully.") // alert
    }
    else { toast("Empty feilds, Fill the detail correctly !"); }
  }

  return (
    <div>
      {  // Rendoring on some condition
        !showchat ? (

          // If user not Joined THe chat by entering username and code, show this.
          <div className="flex flex-col mt-10 m-auto rounded-xl  bg-gray-400  p-6 max-w-[1200px]">

            <h1 className="text-center font-semibold text-[20px]  mb-3">Chat Now</h1>
            <input className="border-2 border-grey-500 w-[350px] md:w-[600px] rounded-md m-auto mb-3 text-[16px] p-2" placeholder="Enter your name" onChange={handleValue} value={data.username} name="username" type="text" />
            <input className="border-2 border-grey-500 w-[350px] md:w-[600px]  rounded-md m-auto mb-3 p-2 text-[16px]" placeholder="Enter Chat Code" onChange={handleValue} value={data.code} name="code" type="text" />
            <input className="bg-blue-400 w-[350px] md:w-[600px] m-auto text-[18px] rounded-md p-2 font-semibold hover:bg-blue-500" onClick={join_chat} type="button" value="Submit" />
            <p className="text-center font-semibold mt-6">* "Share this chat code with the person you wish to connect with for seamless communication."</p>

          </div>

        ):
        (
          // If user joined chat, then show the chat box also passed props to it.
          <Chatbox socket={socket} username={data.username} room={data.code} />
        )}
    </div>
  )
}

