import React, { useEffect, useState } from 'react';
import Bell from "../Best.mp3";  // Importing Audio
import Bell11 from "../sendd.mp3"; // Importing Audio
import Bell22 from "../Shotgun.mp3"; // Importing Audio
import '../App.css';
import { toast } from 'react-toastify'; // Importing toastifiers
import EmojiPicker from 'emoji-picker-react'; // importing emoji picker

export default function Chat({ socket, username, room }) { // getting data as a props
    const [text, setTxt] = useState(""); // Handling Type Message Input

    // it store all converstion between users, intially empty array.
    const [msglist, setMsglist] = useState([]); // Initialize msglist as an array

    //  Controlling Emoji box visibility
    const [showEmojiPicker, setShowEmojiPicker] = useState(false);

    // Making Audio intense
    const Ring = new Audio(Bell);
    const Send = new Audio(Bell11);

    // Envokes when user click the "Send" button
    const sendmesage = async () => {
        if (text !== "") { // in input not empty then run
            const msgdata = { // creating data object to send to backend.
                id: Math.random(), // message id genrating 
                room: room,
                author: username,
                message: text, // user message
                time: new Date(Date.now()).getHours() % 12 + ":" + new Date(Date.now()).getMinutes(), // generates the time in H:M.
            };
            await socket.emit("sendtext", msgdata); // sending data to backend "sendtext" route [33]
            setMsglist([...msglist, msgdata]); // Spread the array to add the new message, adding msg in array
            console.log(msgdata);
            setTxt(""); // making input feild empty after sending msg
            Send.play(); // playing sending sound
        }
    };

    // us particular room me kisi or ne bhi koi msg bheja heto to usko fethch kr rhe he backend se or msglist me add krre he UI me show krne ke liye
    useEffect(() => {
        const handlerecievemsg = (data) => { // data = bckend se
            console.log("reciefe")
            console.log(data)
            setMsglist((msglist) => [...msglist, data]); // data ko msglist meadd to show in UI
            Ring.play(); // audio play
            toast.success("New Message recieved.")

        }
        socket.on("receive_message", handlerecievemsg); //calling "recie_msg" route in bckend and jo bhi data aa rha he bckend se usko is funct me pass kr diya.

        return () => {
            socket.off("receive_message", handlerecievemsg) // off to prevent duplicacy
        }
    }, [socket])

    // selecting and setting emoji in inputbox
    const onEmojiClick = (emojiObject) => {
        setTxt((prevText) => prevText + emojiObject.emoji);
        setShowEmojiPicker(false);
    };

    return (
        <div>
            <div className="flex flex-col justify-center items-center md:flex-row bg-slate-400">
                <h1 className="text-center block bg-gray-400 p-3 font-semibold">Username : {username} <span className="ml-6"> Chat Code : {room}</span>  </h1>
                <div className="bg-gray-400 p-2">
                    <span onClick={() => window.location.reload()} className="ml-4 bg-red-500 hover:bg-red-600 hover:cursor-pointer px-2 py-1 rounded-lg">Join Another Room</span>
                    <span onClick={() => window.location.reload()} className="ml-4 bg-red-500 hover:bg-red-600 hover:cursor-pointer px-2 py-1 rounded-lg">Leave Chat</span>
                </div>
            </div>


            <div className="bg-gray-400  max-w-[600px] p-1 m-auto border-2border-red-600  mt-6  relative">
                <div className="rounded-xl  m-5 h-[400px] hidescroll overflow-y-auto  ">
                    {/* Mapping all messages */}
                    {msglist.map((data) => (
                        <div key={data.id} className={`m-2 max-w-fit px-5 py-2 mb-7 flex flex-col rounded-lg ${data.author === username ? "bg-blue-200 text-right ml-auto  items-end" : "bg-gray-200 text-left mr-auto items-start"}`}
                        >
                            <p className="text-[16px] font-medium">{data.message}</p>
                            <div className="flex text-[12px] gap-1">
                                <p>{data.author} </p>
                                {/* <p>{data.room}</p> */}
                                <p className=""> {data.time}</p>
                            </div>
                        </div>
                    ))}
                    <div className="chat-box  bg-slate-600   absolute bottom-0 left-0 right-0 flex">
                        <button onClick={() => setShowEmojiPicker(!showEmojiPicker)} className="bg-gray-300 hover:bg-gray-400 p-2"></button>

                        <input
                            type="text"
                            value={text}
                            onChange={(e) => setTxt(e.target.value)}
                            placeholder="Enter text"
                            className="w-[80%] border-2 border-grey-600 text-[18px]  p-2 border-grey-600"
                            onKeyPress={(e) => { e.key === "Enter" && sendmesage() }}
                        />
                        <button className="bg-blue-500 hover:bg-blue-600 w-[20%] font-semibold" onClick={sendmesage}>Send</button>
                    </div>
                    {/* will show if click on emoji button */}
                    {showEmojiPicker && (
                        <div className="absolute bottom-[60px] left-0">
                            <EmojiPicker onEmojiClick={onEmojiClick} />
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
